.whatGpt {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.whatGpt__feature {
    display: flex;
}

.whatGpt__feature .features__container__feature {
    margin: 0;
}

.whatGpt__feature .features__container__feature-text{
    max-width: 700px;
}

.whatGpt__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}

.whatGpt__heading h1 {
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 510px;
}

.whatGpt__heading p {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
}

.whatGpt__container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
    width: 100%;
    /* justify-content: center; */
}
.whatGpt__feature .features__container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: flex;
    
}
.whatGpt__feature  .features__container__feature-text {
    margin-top: 0.5rem;
}


.whatGpt__container .features__container__feature{
    flex-direction: column;
    /* margin-inline: 0.1rem; */
}

@media screen and (max-width: 850px) {
   .whatGpt__heading{
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0rem;
   }
   .whatGpt__heading p{
    margin-top: 1rem;
   }

    .whatGpt__heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
    .whatGpt__feature .features__container__feature{
        flex-direction: column;
    }
}
@media screen and (max-width: 650px){
    .whatGpt__feature  .features__container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .whatGpt__feature  .features__container__feature-text{
        margin-top: 0.5rem;
    }

    .whatGpt__heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}
@media screen and (max-width: 350px) {
    .whatGpt__feature .features__container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}